export default {
    state: {
        cars: ''
    },
    mutations: {
        setCarList(state, payload) {
            state.cars = payload
        }
    },
    actions: {
        getCarList({dispatch, commit}, filter) {
            const data = (filter !== undefined) ? filter : ''
            dispatch('sendRequest', {method: 'GET', endpoint: 'car/list/?' + data})
            .then(response => response.json())
            .then(data => {
                commit('setCarList', data)
            })
        }
    },
    getters: {
        getCarList(state) {
            return state.cars
        }
    }
}