                                <span v-if="timer.hours === 1 || timer.hours === 0">{{timer.hours}} час</span>
                                <span v-else-if="timer.hours > 1 && timer.hours < 5">{{timer.hours}} часа</span>
                                <span v-else>{{timer.hours}} часов</span>
                                <span>{{timer.minutes}} минут</span>
                                <span>{{timer.seconds}} секунд</span>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-md-6">
                                <label class="form-label">Дата аукциона</label>
                                <input type="text" class="form-input" :value="detail.lot_status ? String(detail.lot_status.date_auction).replace('  ', 'г.,') + 'ч.' : '-'" disabled>
                            </div>
                            <div class="col-md-6">
                                <label class="form-label">Текущая ставка</label>
                                <input type="text" class="form-input text-danger" :value="'$' + (detail.lot_status ? detail.lot_status.price : '0')" disabled>
